<template>
    <div class="detail">
        <van-nav-bar title="商品详情" left-text="返回" left-arrow fixed @click-left="back" />
        <div class="detail-content">
            <div class="detail-content-img">
                <img class="auto-img" :src="productDetail.large_img" :alt="productDetail.name">
            </div>
            <div class="detail-content-info">
                <div class="content-info-pro">
                    <div class="info-pro-name">
                        <div class="pro-name-cname">
                            {{productDetail.name}}
                        </div>
                        <div class="pro-name-enname">
                            {{productDetail.enname}}
                        </div>
                    </div>
                    <div class="info-pro-price">
                        ￥{{productDetail.price}}
                    </div>
                </div>

                <div class="content-info-rule">
                    <div class="info-rule-item" v-for="(item,index) in productDetail.rules" :key="index">
                        <div class="rule-item-title">
                            {{item.title}}
                        </div>
                        <div class="rule-item-option" :class="{active:item.ruleIndex === i,'large-text':v.title.length >=4}" v-for="(v,i) in item.rule" :key="i" @click="toggleRule(item, i)">
                            {{v.title}}
                        </div>
                    </div>
                </div>

                <div class="content-info-count">
                    <div class="info-count-title">
                        <span>选择数量</span>
                    </div>
                    <div class="info-count-num">
                        <van-stepper v-model="product.count" theme="round" button-size="22" disable-input />
                    </div>
                </div>

                <div class="content-info-desc">
                    <div class="info-desc-title">
                        <span>商品描述</span>
                    </div>
                    <div class="info-desc-text" v-for="(item,index) in productDetail.desc" :key="index">
                        <span>{{ index + 1 }}、</span>
                        <span>{{ item }}</span>
                    </div>
                </div>
            </div>
        </div>
        <van-action-bar>
            <van-action-bar-icon icon="cart-o" text="购物车" :to="{ name: 'shopbag' }" :badge="bagCount == 0 ? '' : bagCount" :color="bagCount > 0 ? '#ff4201' : '#646566'" />
            <van-action-bar-icon icon="like" :text="isLike ? '已收藏' : '未收藏'" :color="isLike ? '#ff4201' : '#646566'" @click="toggleLikeProduct" />
            <van-action-bar-button type="warning" text="加入购物车" @click="addShopbag" />
            <van-action-bar-button type="danger" text="立即购买" />
        </van-action-bar>
    </div>
</template>

<script>
    export { default } from './detail';
</script>

<style lang="less" scoped>
    @import './detail.less';
</style>