import {reactive,toRefs,onMounted,defineComponent} from 'vue';
import {useRoute,useRouter} from 'vue-router';
import { getRequest,postRequest } from '../../request/api';
import url from '../../request/url';
import {token} from '../../libs/util';
import {Toast} from 'vant'
export default defineComponent({
    name:'detail',
    setup () {
        let pid = useRoute().params.pid;
        let router = useRouter();
        let product = { count: 1 };
        let state = reactive({
            bagCount: 0,
            isLike: false,
            product,
            productDetail:{}
        })
        
        //方法
        let back = () => {
            router.back()
        }
        let toggleRule = (item, i) => {
            if(Reflect.get(item,'ruleIndex') === i) return;
            Reflect.set(item,'ruleIndex',i)
        }
        let productDetail = () => {
            getRequest(url.productDetail,{pid}).then(({code,result=[]}) => {
                if(code == 600){
                    let [data = {}] = result;
                    Reflect.set(data,'desc',data.desc.split(/\n/));
                    let rules = [];
                    let ruleData = ["tem", "sugar", "milk", "cream"];
                    ruleData.map(item => {
                        let r = {
                            rule:[]
                        };
                        Reflect.set(r,'title',data[`${item}_desc`]);
                        Reflect.set(r,'ruleIndex',0);
                        Reflect.set(r,'rule',[]);
                        let ruleString = data[item].split("/");
                        ruleString.map((value) => {
                            if (value != "") {
                                r.rule.push({ title: value });
                            }
                        });
                        if (Reflect.get(r.rule,'length') > 0) {
                            rules.push(r);
                        }
                    })

                    Reflect.set(data,'rules',rules);
                    Reflect.set(state,'productDetail',data);
                }else{

                }
            }).catch((err) => {
                console.log("err==>",err);
            });
        };

        let findLike = () => {
            if(!token('token')) return;
            getRequest(url.findlike,{pid}).then(({code,result=[]}) => {
                if(code == 1000){
                    Reflect.set(state,'isLike',!!Reflect.get(result,'length'))
                }else{

                }
                
            }).catch((err) => {
                console.log("err==>",err);
            });
        };

        let shopBagCount = () => {
            if(!token('token')) return;
            getRequest(url.findAllShopcart,{pid}).then(({code,result=[]}) => {
                if(code == 5000){
                    state.bagCount = result.length;
                }else{

                }
            }).catch((err) => {
                console.log("err==>",err);
            });
        };

        let toggleLikeProduct = () => {
            if(!token('token')) {
                Toast('请先登录');
                return router.push({ name: "login" });
            }
            let url = state.isLike ? "/notlike" : "/like";
            postRequest(url,{pid}).then(({code,msg}) => {
                if(code == 800 || code == 900){
                    Reflect.set(state,'isLike',!Reflect.get(state,'isLike'));
                }
                Toast(msg);
            }).catch((err) => {
                console.log("err==>",err);
            });
        }
        


        let addShopbag = () => {
            if(!token('token')) {
                Toast('请先登录');
                return router.push({ name: "login" });
            }
            let data = Object.create(null);
            Reflect.set(data,'pid',pid);
            Reflect.set(data,'count',state.product.count);
            let rs = [];
            state.productDetail.rules.map((v) => {
                rs.push(v.rule[v.ruleIndex].title);
            });
            Reflect.set(data,'rule',rs.join("/"));
            postRequest(url.addShopcart,data).then(({code,status = '',msg}) => {
                if(code == 3000 && status == 1) state.bagCount++;
                Toast(msg);
            }).catch((err) => {
                console.log("err==>",err);
            });
        }

        onMounted(() =>{
            productDetail();
            findLike();
            shopBagCount();
        })
        return {
            ...toRefs(state),
            back,
            toggleRule,
            addShopbag,
            toggleLikeProduct
        }
    }
})